body {
  font-family: 'Roboto', sans-serif;
  background-color: #e5e5e5;
}

.header {
  font-weight: normal;
  font-size: inherit;
  line-height: inherit;
}

.weather {
  width: 621px;
  height: 462px;
  margin: auto;
  margin-top: 50px;
  padding: 10px;
  font-size: 24px;
  line-height: 28px;
  background-color: #fff;
}

.weather__container {
  height: 435px;
  border: 1px solid #000;
}

.weather__main {
  height: 388px;
}

.form {
  position: relative;
  padding: 8px 16px;
  border-bottom: 1px solid #000;
}

.form__input {
  width: 542px;
  padding: 0;
  font-size: 18px;
  line-height: 21px;
  border: none;
  outline: none;
}

.form__input::placeholder {
  font-size: 18px;
  line-height: 21px;
  color: #696969;
}

.form__search-btn {
  position: absolute;
  padding: 0;
  right: 11px;
  width: 30px;
  height: 30px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  background-image: url('/img/find.svg');
  background-repeat: no-repeat;
}

.weather__tabs {
  width: 301px;
  flex-wrap: wrap-reverse;
  font-size: 0;
  border-right: 1px solid #000;
}

.wrap-input {
  position: absolute;
  opacity: 0;
}

.wrap-btn {
  display: inline-block;
  width: 100px;
  height: 44px;
  padding: 12px 0;
  text-align: center;
  vertical-align: middle;
  font-size: 18px;
  line-height: 21px;
  user-select: none;
  cursor: pointer;
  border-top: 1px solid #000;
}

.wrap-btn:not(:last-of-type) {
  border-right: 1px solid #000;
}

.wrap-input:checked+.wrap-btn {
  color: #fff;
  background-color: #000;
}

.weather__tab {
  display: none;
}

.weather__tab-container {
  width: 299px;
  height: 345px;
  padding-top: 33px;
  padding-left: 18px;
  font-size: 24px;
  line-height: 28px;
}

#wrap-btn_now:checked~.tab-now,
#wrap-btn_details:checked~.tab-details,
#wrap-btn_forecast:checked~.tab-forecast {
  display: block;
}

.header_details {
  margin-bottom: 42px;
}

.tab-details__list {
  margin-bottom: 46px;
}

.tab-details__list-item:not(:last-child) {
  margin-bottom: 14px;
}

.weather__tab-container_now {
  flex-direction: column;
  justify-content: space-between;
  background-image: url('/img/cloud.png');
  background-repeat: no-repeat;
  background-position: center;
}

.header_now {
  font-size: 70px;
  line-height: 82px;
}

.tab-now__bottom {
  margin-bottom: 17px;
  padding-right: 26px;
  justify-content: space-between;
  align-items: center;
}

.tab-now__add-to-fav {
  width: 24px;
  height: 25px;
}

.weather__tab-container_forecast {
  padding-right: 16px;
  overflow-y: scroll;
}

.weather__locations::-webkit-scrollbar,
.weather__tab-container_forecast::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.header_forecast {
  margin-bottom: 7px;
}

.tab-forecast__list-item {
  height: 120px;
  margin-bottom: 9px;
  padding: 10px;
  flex-direction: column;
  justify-content: space-between;
  font-size: 18px;
  line-height: 21px;
  border: 1px solid #000000;
}

.tab-forecast__item-top {
  justify-content: space-between;
}

.tab-forecast__item-bottom {
  justify-content: space-between;
}

.tab-forecast__weather {
  padding-bottom: 31px;
  background-image: url('/img/cloud_mini.png');
  background-repeat: no-repeat;
  background-position: bottom;
}

.tab-forecast__temp {
  margin-bottom: 5px;
}

.weather__locations {
  width: 298px;
  overflow-y: scroll;
}

.header_locations {
  padding-left: 18px;
  padding-top: 33px;
  margin-bottom: 7px;
  border-bottom: 1px solid #000;
}

.weather__locations-list {
  padding-left: 18px;
  padding-right: 18px;
  padding-top: 33px;
}

.weather__locations-list-item {
  margin-bottom: 14px;
  justify-content: space-between;
}

.weather__location-del {
  width: 17px;
  height: 16px;
  align-self: center;
  background-color: #000000;
  background-image: url('/img/close.png');
  background-repeat: no-repeat;
}